.test {
    flex-grow: 1;
    display: flex;
    align-items: center;
}


.progressBar {
    height: 4px;
    background-color: #2E3131;
    transition: all ease-in-out 300ms;
}

.headline {
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: 700;
}
.variants {
    width: 100%;
}

.variants ul {
    list-style: none;
}

.variants li {
    font-size: 24px;
    margin: 10px;
    display: flex;
    justify-content: center;
    cursor: pointer;
}